import { connect } from 'react-redux';
import Login from './Login';
import { signIn } from '../../redux/reducers/authReducer';
import React from 'react';
import { Navigate } from 'react-router-dom';
import { getIsAuth, getCapthcaUrl } from '../../redux/selectors/authSelectors';
import { useEffect } from 'react';

const LoginContainer = (props) => {
  useEffect(() => {}, [props.authInfo]);

  if (props.authInfo) {
    return <Navigate to={'/Profile'} />;
  } else if (!props.authInfo) {
    return <Login {...props} signIn={props.signIn} />;
  }
};

let mapStateToProps = (state) => {
  return {
    authInfo: getIsAuth(state),
    captchaUrl: getCapthcaUrl(state),
  };
};

export default connect(mapStateToProps, {
  signIn,
})(LoginContainer);
